<template>
  <div class="login">
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)" />
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="mobile" class="inp" maxlength="11" placeholder="请输入手机号码" type="text">
        </div>
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="4" placeholder="请输入图形验证码" type="text">
          <img v-if="picUrl" :src="picUrl" @click="getPicCode" alt="">
        </div>
        <div class="form-item">
          <input v-model="msgCode" class="inp" maxlength="6" placeholder="请输入短信验证码" type="text">
          <button @click="getCode">
            {{ second === totalSecond ? '获取验证码' : second + `秒后重新发送` }}
          </button>
        </div>
      </div>

      <div @click="login" class="login-btn">登录</div>
    </div>
  </div>
</template>

<script>
import { getPicCode, getMsgCode, codeLogin } from '@/api/login'
import { Toast } from 'vant'
export default {
  name: 'LoginPage',
  data () {
    return {
      picUrl: '', // 图片验证码地址
      picKey: '', // 图片验证码key
      totalSecond: 60, // 总秒数
      second: 60, // 倒计时的秒数
      timer: null, // 定时器
      mobile: '', // 手机号
      picCode: '', // 图片验证码
      msgCode: '' // 短信验证码
    }
  },
  async created () {
    // 写成函数是因为点击图形验证码的时候需要发送请求，显得简洁一些
    // 直接写在created里面，因为需要使用this
    // 调用接口获取图片验证码
    // 这里只调用图片验证码函数，是想进入页面就发送一次请求
    this.getPicCode()
  },
  methods: {
    // 获取图片验证码 (写在methods里面，因为需要使用this)
    async getPicCode () {
      // await 后面的请求被抽离到api文件夹中
      // 优点：简洁 可复用 模块化 便于维护
      // 因为getPicCode函数是异步函数，所以await只能用于async函数中
      const res = await getPicCode()
      this.picUrl = res.data.data.base64
      this.picKey = res.data.data.key
    },
    // 获取短信验证码
    // 这里加async是因为需要使用await，await只能用于async函数中
    async getCode () {
      // 请求前对手机号和验证码进行校验
      if (!this.validFn()) return
      // 对发送请求进行节流
      if (this.timer) return
      // 发送请求，获取短信验证码
      await getMsgCode(this.picCode, this.picKey, this.mobile)
      Toast('发送成功，请注意查收')
      // 开启定时器
      this.timer = setInterval(() => {
        this.second--
        // 如果 second 减到小于 1 关闭定时器，清除定时器，复原second
        if (this.second < 1) {
          clearInterval(this.timer)
          this.second = this.totalSecond
          // 清除定时器
          this.timer = null
        }
      }, 1000)
      // 发送请求， 请求提示
      Toast('发送成功，请注意查收')
    },
    // 登录
    async login () {
      // 复用判断手机号和图形验证码
      if (!this.validFn()) return
      // 复用判断短信验证码
      // 这里需要对短信验证码进行正则匹配
      // 因为短信验证码是数字，所以需要使用正则匹配
      if (!/^\d{6}$/.test(this.msgCode)) {
        this.$toast('请输入正确的手机验证码')
        return
      }
      // 调用接口进行登录
      const res = await codeLogin(this.mobile, this.msgCode)
      // 存储到vuex里面
      this.$store.commit('user/setUserInfo', res.data.data)
      // 登录成功后跳转到首页
      const url = this.$route.query.backUrl || '/'
      this.$router.push(url)
      Toast('登录成功')
    },
    // 对手机号和图形验证码验证一起进行封装
    // 优点：简洁 可复用
    validFn () {
      // 这里使用到了正则匹配，使用model不需要使用修饰符
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        Toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        Toast('请输入正确的图形验证码')
        return false
      }
      return true
    }
  },
  // 离开页面销毁定时器
  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;

    h3 {
      font-size: 26px;
      font-weight: normal;
    }

    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;

    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }

    img {
      width: 94px;
      height: 31px;
    }

    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg, #ecb53c, #ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, .1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
